import React from 'react';
import PropTypes from 'prop-types';
import { Divider, Select } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Box from '@material-ui/core/Box';
import { makeStyles } from '@material-ui/core/styles';
import { bleDevice } from './ble'

const useStyles = makeStyles((theme) => ({
  box: {
    marginTop: theme.spacing(1.0),
    marginBottom: theme.spacing(0.5),
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  title: {
    fontSize: '150%',
    fontWeight: 'bold',
  },
  button: {
    marginTop: theme.spacing(2),    
    fontSize : '150%',
  }
}));

const isWordId            = 0;
const isCfThreshold       = 1;
const isVdThreshold       = 2;
const isGain              = 3;
const isNoiseSuppressor   = 4;
const isBeamForming       = 5;

const parametersValue = [
  '0', '0', '0', '0', '0', '0',
];

const stringToNumber = (array) => {
  for (let i = 0; i < array.length; i++) {
    array[i] = array[i] - 0; // string -> number
  }
}

const sendButtonClicked = () => {
  stringToNumber(parametersValue);
  bleDevice.writeValueWithoutResponse (bleDevice.bleInfo.device,
                                       bleDevice.bleInfo.characteristicInfo.voiceRecogParams.characteristic,
                                       [parametersValue[isWordId], 
                                        parametersValue[isCfThreshold],
                                        (parametersValue[isVdThreshold] >> 8),
                                        (parametersValue[isVdThreshold] & 0xFF),
                                        parametersValue[isGain],
                                        parametersValue[isNoiseSuppressor],
                                        parametersValue[isBeamForming]]);
}

function PageIsVoiceRecogSettings (props) {
  const classes = useStyles();
  return (
    <div>
      <SelectConfideceThreshold wordInfo = {props.wordInfo} />

      <SelectVoiceDetectionThreshold wordInfo = {props.wordInfo}/>

      <SelectGain wordInfo = {props.wordInfo} />

      <SelectNoiseSuppressor wordInfo = {props.wordInfo} />

      <SelectBeamForming wordInfo = {props.wordInfo} />

      <Button 
      className = {classes.button} 
      variant = 'outlined' 
      color = 'primary' 
      fontWeight = 'bold'
      onClick = {sendButtonClicked}
      >
        SEND
      </Button>

    </div>
  );
}

function InputParameterValue (props) {
  const classes = useStyles();
  const [paramvalue, setParamValue] = React.useState(parametersValue[props.parameterId]);

  const handleParamValueChanged = (event) => {
    let value = event.target.value;

    if (value > props.maxValue) {
      value = props.maxValue;
    }
    else if (value < props.minValue) {
      value = props.minValue;
    }
    else {
      // nothing
    }

    if (props.wordInfo.length <= 1) {
      console.log('No wordInfo');
    }
    else if (props.wordId === '0'){
      for (let i = 0; i < props.wordInfo.length; i++) {
        props.wordInfo[i].parameters[props.parameterId] = value - 0;  // string -> number
      }
    }
    else {
      props.wordInfo[props.wordId].parameters[props.parameterId] = value - 0;  // string -> number
    }

    parametersValue[props.parameterId] = value;
    setParamValue(parametersValue[props.parameterId]);
  }

  React.useEffect(() => {
    if (props.wordInfo.length <= 1) {
      console.log('No wordInfo');
    }
    else{
      parametersValue[props.parameterId] = props.wordInfo[props.wordId].parameters[props.parameterId] + ''; // number -> string
      setParamValue(parametersValue[props.parameterId]);
    }
    
  }, [props.wordInfo, props.wordId]);

  return (
    <div>
      <Box className = {classes.box} mt = {1} >
        <TextField
          id = 'param-value-text-field'
          label='Value'
          type='number'
          // defaultValue = {0}
          value = {paramvalue}
          onChange = {handleParamValueChanged}
        />
      </Box>
    </div>
  );
}

function SelectConfideceThreshold (props) {
  const classes = useStyles();
  const [wordId, setWordId] = React.useState('1');

  const handleWordIdChanged = (event) => {
    const value = event.target.value;

    if (value === '-1') {
      // nothing
    }
    else {
      parametersValue[isWordId] = value;
      setWordId(parametersValue[isWordId]);
    }    
  }

  React.useEffect(() => {
    if (props.wordInfo.length <= 1) {
      console.log('No wordInfo');
    }
    else{
      parametersValue[isWordId] = wordId;
      setWordId(parametersValue[isWordId]);
    }
    
  }, [props.wordInfo]);

  return (
    <div>
      <Box className = {classes.box}>
        <Typography className = {classes.title}>
          Confidence Threshold
        </Typography>

        <Box display = 'flex' flexDirection = 'row'>
          <Box display = 'flex' flexDirection = 'column'>
          {
            ((props.wordInfo.length <= 1)
            ?
              (
                <FormControl className={classes.formControl}>
                <InputLabel >Word ID</InputLabel>
                <Select
                  native
                  value={wordId}
                  onChange={handleWordIdChanged}
                >
                  <option aria-label='None' value={'-1'} />
                </Select>
                </FormControl>
              )
            :
              (
                <FormControl className={classes.formControl}>
                <InputLabel >Word ID</InputLabel>
                <Select
                  native
                  value={wordId}
                  onChange={handleWordIdChanged}
                >
                  {
                    props.wordInfo.map((info, index) => (
                      ((index === 0) ? 
                        (
                          <option value={'0'} key={index}>All</option>
                        )
                      :
                        (
                        <option value={info.wordId} key={index}>
                          No. {info.wordId}
                        </option>
                        )
                      )
                    ))
                  }
                </Select>
                </FormControl>
              )
            )
          }
          </Box>

          <Box display = 'flex' flexDirection = 'column' ml = {1.0}> 
            <InputParameterValue
            wordInfo = {props.wordInfo}
            wordId = {wordId}
            parameterId = {isCfThreshold}
            maxValue = {200}
            minValue = {0}
            />
          </Box>
        </Box>
      </Box>
    </div>

  );
}

function SelectVoiceDetectionThreshold (props) {
  const classes = useStyles();
  return (
    <div>
      <Box className = {classes.box} mt = {1}>
        <Typography className = {classes.title}>
          Voice Detection Threshold
        </Typography>

        <InputParameterValue
        wordInfo = {props.wordInfo}
        wordId = {1}
        parameterId = {isVdThreshold}
        maxValue = {15000}
        minValue = {0}
        />
      </Box>
    </div>
  );
}

function SelectGain (props) {
  const classes = useStyles();  
  return (
    <div>
      <Box className = {classes.box} mt = {1} >
        <Typography className = {classes.title}>
          Gain
        </Typography>

        <InputParameterValue
        wordInfo = {props.wordInfo}
        wordId = {1}
        parameterId = {isGain}
        maxValue = {9}
        minValue = {0}
        />
      </Box>
    </div>
  );
}

function SelectNoiseSuppressor (props) {
  const classes = useStyles();
  return (
    <div>
      <Box className = {classes.box} mt = {1} >
        <Typography className = {classes.title}>
          Noise Suppressor
        </Typography>

        <InputParameterValue
        wordInfo = {props.wordInfo}
        wordId = {1}
        parameterId = {isNoiseSuppressor}
        maxValue = {7}
        minValue = {0}
        />
      </Box>
    </div>
  );
}

function SelectBeamForming (props) {
  const classes = useStyles();  
  return (
    <div>
      <Box className = {classes.box} mt = {1} >
        <Typography className = {classes.title}>
          Beam Forming
        </Typography>

        <InputParameterValue
        wordInfo = {props.wordInfo}
        wordId = {1}
        parameterId = {isBeamForming}
        maxValue = {7}
        minValue = {0}
        />
      </Box>
    </div>
  );
}


PageIsVoiceRecogSettings.propTypes = {
    window: PropTypes.func,
  };

export { PageIsVoiceRecogSettings };